/* src/components/DriveVideoEmbed.css */

.custom-video-wrapper {
  background-color: #0A0A1A;
  max-width: 650px; 
  margin: 20px auto;
  border: 1px solid #e0e0e0; /* Viền nhẹ nhàng hơn */
  border-radius: 8px; /* Bo tròn góc */
  overflow: hidden;
  font-family: Arial, sans-serif;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.08); /* Thêm đổ bóng nhẹ */
}

/* Kiểu cho Tiêu đề (header) - Tên mới: video-header-bar và video-header-title */
.video-header-bar {
  background-color: #e6ffe6; /* Màu xanh lá nhạt hơn */
  padding: 10px 20px;
  text-align: center;
  border-bottom: 1px solid #d4ffd4;
}

.video-header-title {
  font-size: 1.3em; /* Kích thước chữ nhỏ hơn một chút */
  font-weight: bold;
  color: #00FC45; /* Màu chữ xanh lá nổi bật */
}


/* Container Iframe */
.video-iframe-container {
  position: relative;
  width: 100%;
  padding-bottom: 56.25%; 
  height: 0;
  background-color: black; /* Đảm bảo nền đen nếu video chưa load */
}

.video-iframe-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/* --- LỚP PHỦ ĐỂ CHE NÚT POP-OUT --- */
.popout-cover {
  position: absolute;
  top: 0; 
  right: 0; 
  /* THỬ TĂNG LÊN */
  width: 60px; /* Tăng chiều ngang */
  height: 60px; /* Tăng chiều dọc */
  background-color: black;
  z-index: 10;
}

/* --- KIỂU CHO LỚP PHỦ LOGO --- */
.custom-logo-overlay {
  position: absolute;
  top: 0; 
  right: 0; 
  
  /* Đảm bảo kích thước đủ lớn để che hoàn toàn biểu tượng Pop-out */
  width: 58px; /* Hoặc kích thước bạn đã tinh chỉnh để che hết Pop-out */
  height: 58px; /* Hoặc kích thước bạn đã tinh chỉnh để che hết Pop-out */
  
  /* Sử dụng màu nền trong suốt hoặc màu đen tùy ý nếu logo của bạn không che hết */
  background-color: white; /* Hoặc black nếu logo nhỏ và bạn muốn che thêm */
  
  z-index: 10; /* Đảm bảo nằm trên iframe */

  /* Căn giữa logo bên trong lớp phủ */
  display: flex;
  justify-content: center;
  align-items: center;
}

.overlay-logo {
  max-width: 80%; /* Giúp logo nhỏ lại để vừa trong khung phủ */
  max-height: 80%;
  object-fit: contain; /* Đảm bảo logo không bị biến dạng */
}

/* Kiểu cho Thông tin bổ sung (footer) */
.video-meta-info {
  display: flex;
  justify-content: space-between;
  align-items: center; /* Căn giữa theo chiều dọc */
  padding: 10px 20px;
  background-color: #f7f7f7; /* Nền nhẹ nhàng hơn */
  font-size: 0.9em;
  color: #777; /* Màu chữ dịu hơn */
  border-top: 1px solid #eee;
}

.video-meta-info .category {
  font-weight: bold;
  color: #38761d; 
}

.video-meta-info .code {
  color: #999;
}

.video-placeholder {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #000;
  overflow: hidden;
  cursor: pointer;
}

/* Hiệu ứng nền mờ tối */
.video-placeholder::before {
  content: "";
  position: absolute;
  inset: 0;
  background: radial-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.85));
  backdrop-filter: blur(6px);
  z-index: 0;
}

/* Logo overlay (góc trên trái) */
.video-placeholder .overlay-logo {
  position: absolute;
  top: 20px;
  left: 20px;
  height: 50px;
  z-index: 2;
  opacity: 0.9;
}

/* Nút play chính giữa */
.video-placeholder .play-btn {
  width: 80px;
  height: 80px;
  background: rgba(255, 255, 255, 0.9);
  clip-path: polygon(25% 20%, 25% 80%, 80% 50%);
  border-radius: 50%;
  z-index: 3;
  transition: transform 0.25s ease, background 0.25s ease;
}

/* Hover: hiệu ứng phóng to và sáng hơn */
.video-placeholder:hover .play-btn {
  transform: scale(1.1);
  background: rgba(255, 255, 255, 1);
}

